import Vue from 'vue'
import Router from 'vue-router'
import Home from './views/Home.vue'

Vue.use(Router)

export default new Router({
  base: '/',
  mode: 'hash',
  routes: [
    {
      path: '/',
      name: 'home',
      component: Home
    },
    {
      path: '/articles?tags=Robots',
      name: 'robots',
      component: () => import(/* webpackChunkName: "robots" */ './views/Articles.vue'),
      alias: '/robots'
    },
    // {
    //   path: '/sponsors',
    //   name: 'sponsors',
    //   component: () => import(/* webpackChunkName: "sponsors" */ './views/Sponsors.vue')
    // },
    {
      path: '/contact',
      name: 'contact',
      component: () => import(/* webpackChunkName: "contact" */ './views/Contact.vue')
    },
    {
      path: '/profiles',
      name: 'profiles',
      component: () => import(/* webpackChunkName: "contact" */ './views/Profiles.vue')
    },
    {
      path: '/articles',
      name: 'articles',
      component: () => import(/* webpackChunkName: "articles" */ './views/Articles.vue')
    },
    {
      path: '/article/:id',
      name: 'article',
      component: () => import(/* webpackChunkName: "article" */ './views/Article.vue')
    },
    {
      path: '/404',
      component: () => import(/* webpackChunkName: "article" */ './views/404.vue')
    },
    {
      path: '*',
      redirect: '/404'
    }
  ]
})
